body,html{
    width: 100%;
    margin: 0;
    padding: 0;
}
.top{
    width: 100%;
    height: 88px;
    img{
        width: 190px;
        margin-left: 150px;
        margin-top: 27px;
    }
}
.center{
    background:#026340 url(https://img.alicdn.com/tfs/TB1JDJRQ1L2gK0jSZFmXXc7iXXa-1190-600.jpg) no-repeat center;
    width: 100%;
    height: 600px;
    position: relative;
    .center-main{
        width: 330px;
        height: 380px;
        padding: 10px 20px;
        background-color: #fff;
        position: absolute;
        right: 170px;
        bottom: 100px;
        .center-top{
            font-weight: bold;
            width: 66px;
            height: 26px;
            line-height: 26px;
            border-bottom: 2px solid #000;
        }
        .hint{
            display: none;
            height: 30px;
            border: 1px solid orange;
            width: 100%;
            background-color: #fef2f2;
            line-height: 30px;
            text-indent: 5px;
            font-size: 13px;
            span{
                display: inline-block;
                width: 20px;
                height: 20px;
                border-radius: 50%;
                background-color: orange;
                color: #fff;
                margin-right: 5px;
                line-height: 20px;
                text-indent: 8px;
                font-weight: bold;
                font-size: 14px;
            }
        }
        img{
            width: 40px;
            margin-bottom: 10px;
        }
        input{
            height: 36px;
            width: 286px;
            position: relative;
            top: -26px;
            outline: none;
            border: 1px solid gray;
            text-indent: 10px;
        }
        .log{
            width: 100%;
            height: 40px;
            line-height: 40px;
            text-align: center;
            color: #fff;
            background-color: #ff0036;
        }
        .detail{
            font-size: 13px;
            color: rgb(108, 108, 108);
            padding-left: 200px;
            width: 100%;
            height: 20px;
            margin-top: 10px;
            text-indent: -64px;
            line-height: 20px;
            span{
                margin-right: 10px;
            }
            a{
                color:  rgb(108, 108, 108);
                text-decoration: none;
            }
            
        }
    }
    
}
